@use "../../Styles/partials/fonts" as *;
@use "../../Styles/partials/mixins" as *;
@use "../../Styles/partials/variables" as *;

.whyHireMe {
    background-color: $black;
    width: 100%;
    padding: 1rem 0rem 0rem;

    @include desktop {
        padding: 1rem 0rem 5rem;
    }

    &__main-title {
        text-align: center;
        padding: 3rem 0rem 1.5rem;
        color: $white;
        font-family: $Montserrat;
        font-weight: 700;
        font-size: 1.8rem;
        
        @include tablet {
            font-size: 2.4rem;
            padding: 3rem 0rem 0rem;
        }
    }

    &__logo {
        width: 50px;
        height: 50px;
        color: $white;

        @include tablet {
            width: 60px;
            height: 60px;
        }
    }

    &__container {
        @include desktop {
            display: flex;
            flex-direction: row;
            width: 100%;
        }
    }

    &__sub-container {
        display: flex;
        flex-direction: column;
        align-items: center;
        
        @include desktop {
            width: 27%;
            margin: 0% 3% 0%;
        }

        @include xl-desktop {
            width: 40%;
            margin: 0% 0% 0%;
        }
    }

    &__text-container {
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    &__section-title {
        color: $white;
        margin: 3% 0% 5%;
        font-family: $Montserrat;
        font-weight: 700;
        font-size: 1.5rem;
        text-align: center;

        @include tablet {
            margin: 2% 0% 1%;
        }

        @include desktop {
            text-align: center;
            width: 400px;
        }
    }

    &__description {
        text-align: center;
        width: 250px;
        margin: 2% 0% 5%;
        font-family: $Montserrat;
        font-weight: 400;
        color: $white;

        @include tablet {
            width: 500px;
            margin: 2% 0% 3%;
        }

        @include desktop {
            width: 400px;
        }
    }

    &__description--two {
        text-align: center;
        width: 250px;
        margin: 2% 0% 10%;
        font-family: $Montserrat;
        font-weight: 400;
        color: $white;

        @include tablet {
            width: 500px;
            margin: 0% 0% 7%;
        }

        @include desktop {
            width: 400px;
        }
    }
}